.articles {
  grid-gap: 20px;
  max-width: calc(100vw - 80px);
  grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
  margin: 0 auto 30px;
  padding: 0;
  list-style: none;
  display: grid;
}

.articles li {
  text-align: center;
  height: max-content;
  overflow: hidden;
}

.articles img {
  width: 100%;
  height: 200px;
  object-fit: cover;
  display: block;
}

.articles a {
  color: #2a2a2a;
  text-decoration: none;
}

